      <p class="lead">{{ totalResults }} Results Found</p>
      <div class="row gx-0 row-cols-2 pb-5">
        <CatalogPlaceholder v-if="loading" />
          <div v-else class="col-sm-2" v-for="result in shows" :key="result">
              <div class="card text-center text-bg-dark">
                  <router-link :to="'/watch-tv/' + result.id">
                      <img :src="'https://image.tmdb.org/t/p/w342' + result.poster_path"
                      class="card-img-top poster" :alt="result.name">
                  </router-link>
                  <div class="card-body">
                      <h5 class="card-title">{{truncate(result.name,48)}}</h5>
                      <h6 class="card-subtitle mb-2 text-muted">{{result.release_date}}</h6>
                  </div>
                  <div class="card-footer">
                    <router-link class="btn btn-primary" :to="'/watch-tv/' + result.id"><font-awesome-icon icon="fa-solid fa-play" /> Watch Now</router-link>
                  </div>
              </div>
          </div>
      </div>
      <nav aria-label="Page navigation" class="pt-5 pb-5">
          <ul class="pagination justify-content-center">
              <li v-if="previousPage > 0" class="page-item">
                  <a class="page-link" :href="'/shows/' + genre + '/' + previousPage">Previous</a>
              </li>
              <li v-else class="page-item disabled">
                  <a class="page-link" href="#">Previous</a>
              </li>
              <li class="page-item">
                  <a v-if="currentPage !== '1'"
                  class="page-link" :href="'/shows/' + genre + '/1'">
                  1
                  </a>
                  <a v-else
                  class="page-link active">
                  1
                  </a>
              </li>
              <li class="page-item">
                  <a v-if="currentPage !== '1' && currentPage !== totalPages.toString()"
                  class="page-link active">
                  {{currentPage}}
                  </a>
                  <a class="page-link disabled" v-else>
                    ...
                  </a>
              </li>
              <li class="page-item">
                  <a v-if="currentPage !== totalPages"
                  class="page-link" :href="'/shows/' + genre + '/' + totalPages">
                  {{totalPages}}
                  </a>
                  <a v-else
                  class="page-link active">
                  {{totalPages}}
                  </a>
              </li>
              <li v-if="nextPage <= totalPages" class="page-item">
                  <a class="page-link" :href="'/shows/' + genre + '/' + nextPage">Next</a>
              </li>
              <li v-else class="page-item disabled">
                  <a class="page-link" href="#">Next</a>
              </li>
          </ul>
      </nav>
      <div v-if="currentDomain === 'localhost'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075752' loading='lazy' src='//acceptable.a-ads.com/2075752' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
