      </div>
      <div v-else-if="currentDomain === 'movielair.lol'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075764' loading='lazy' src='//acceptable.a-ads.com/2075764' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.quest'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075766' loading='lazy' src='//acceptable.a-ads.com/2075766' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.vip'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075767' loading='lazy' src='//acceptable.a-ads.com/2075767' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.biz'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075768' loading='lazy' src='//acceptable.a-ads.com/2075768' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.club'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075770' loading='lazy' src='//acceptable.a-ads.com/2075770' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.co.uk'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075771' loading='lazy' src='//acceptable.a-ads.com/2075771' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.pics'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075775' loading='lazy' src='//acceptable.a-ads.com/2075775' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.us'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075778' loading='lazy' src='//acceptable.a-ads.com/2075778' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
      <div v-else-if="currentDomain === 'movielair.xyz'" class="pb-5 pt-5 text-center container">
        <iframe data-aa='2075780' loading='lazy' src='//acceptable.a-ads.com/2075780' style='border:0px; padding:0; width:100%; height:100%; overflow:hidden; background-color: transparent;'></iframe>
      </div>
