  </template>

<script lang="ts">
import { defineComponent } from 'vue'
import { getShowGenres, getShowsByGenre } from '@/services/IMDBService'
import CatalogPlaceholder from '@/components/CatalogPlaceholder.vue'

export default defineComponent({
  name: 'BrowseShowGenreView',
  data () {
    return {
      genre: this.$route.params.genre,
      shows: null,
      totalResults: 0,
      totalPages: 0,
      currentPage: this.$route.params.page,
      previousPage: Number(this.$route.params.page) - 1,
      nextPage: Number(this.$route.params.page) + 1,
      currentDomain: window.location.hostname,
      genreTitle: '',
      loading: true
    }
  },
  components: {
    CatalogPlaceholder
  },
  created () {
    this.getShowGenres()
  },
  methods: {
    truncate (str, n) {
      return (str.length > n) ? str.slice(0, n - 1) + '...' : str
    },
    getShowGenres () {
      getShowGenres().then(r => {
        // find item in array with id of this.genre
        const g = r.genres.find(item => item.id === Number(this.genre))
        this.genreTitle = g.name
        document.title = 'Browse Free ' + this.genreTitle + ' TV Shows Online - MovieLair'
      })
      if (this.currentPage === undefined) {
        this.currentPage = '1'
      }
      getShowsByGenre(this.genre, this.currentPage).then(r => {
        this.shows = r.results
        this.totalResults = r.total_results
        this.totalPages = r.total_pages
      })
      this.loading = false
    }
  }
})
</script>

